<template>
  <div id="vote">
    <el-container class="vote_container">
      <el-header height="60px">
        <el-button type="info" class="vote-btn" @click="homepage"></el-button>
        <span></span>
      </el-header>
      <el-container>
        <el-aside width="250px">
          <el-menu :unique-opened="true" :router="true" :default-active="activePath">
            <el-submenu :index="adminlist.id + ''" v-show="adminshow === 'admin'">
              <!--  -->
              <template slot="title">
                <i class="el-icon-s-data"></i>
                <span>{{ adminlist.authname }}</span>
              </template>
                <!--  -->
                <el-menu-item :index="'/' + adminlist.children.path"
                  @click="isactivePath('/' + adminlist.children.path)">
                  <template slot="title">
                    <i class="el-icon-menu"></i>
                    <span>{{ adminlist.children.authname }}</span>
                  </template>
                </el-menu-item>
            </el-submenu>
            <el-submenu :index="item.id + ''" v-for="item in menulist" :key="item.id">
              <!--  -->
              <template slot="title">
                <i class="el-icon-s-data"></i>
                <span>{{ item.authname }}</span>
              </template>
                <!--  -->
                <el-menu-item :index="'/' + item1.path"
                  v-for="item1 in item.children" :key="item1.id"
                  @click="isactivePath('/' + item1.path)">
                  <template slot="title">
                    <i class="el-icon-menu"></i>
                    <span>{{ item1.authname }}</span>
                  </template>
                </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import api from '../../api'

export default {
  name: 'vote',
  data() {
    return {
      // 
      menulist: [
        {
          id: 1,
          authname: '',
          children: [
            { id: 1, authname: '', path: 'welcome' },
          ]
        },
        {
          id: 2,
          authname: '',
          children: [
            { id: 2, authname: '', path: 'create' },
            { id: 3, authname: '', path: 'created' },
            { id: 4, authname: '', path: 'voted' },
          ]
        },
        {
          id: 3,
          authname: '',
          children: [
            { id: 5, authname: '', path: 'tickets' },
          ]
        },
      ],
      // 
      adminlist: {
        id: 4,
        authname: '',
        children: { id: 1, authname: '', path: 'admin' },
      },
      adminshow: '',
      activePath: '',
    }
  },
  created() {
    this.getAdmin()
    this.activePath = window.sessionStorage.getItem('activePath')
  },
  methods: {
    async getAdmin() {
      const { data: res } = await api.get('/userinfo')
      if (res.meta.status !== 200) {
        return
      }
      console.log(res)
      this.adminshow = res.data.name
    },
    isactivePath(activePath) {
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
    },
    homepage() {
      this.$router.push('/')
    }
  }
}
</script>

